export default {
  data () {
    return {
      title: '搜索页',
      inputClearValue: '',
      showClearIcon: false,
      searchInfo: '森海塞尔',
      history: ['耳机', '蓝牙音箱', '耳机', '蓝牙音箱', '耳机', '蓝', '耳机耳机耳机', '蓝牙音箱'],
      hot: ['耳机', '蓝牙音箱', '耳机', '蓝牙音箱'],
      type: ''
    }
  },
  onLoad(option) {
    this.type = option.type
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    clearIcon() {
    	this.inputClearValue = ''
    	this.showClearIcon = false
    },
    goGood(item) {
      uni.navigateTo({
        url: 'goods?class=' + item + '&type=' + this.type
      })
    },
    clearInput(event) {
      console.log(event.target.value)
    	this.inputClearValue = event.target.value
    	if (event.target.value.length > 0) {
    		this.showClearIcon = true
    	} else {
    		this.showClearIcon = false
    	}
    },
    searchAction(e) {
      uni.navigateTo({
        url: 'goods?class=' + e.detail.value + '&type=' + this.type
      })
    },
    delHistory() {
      let that = this
      uni.showModal({
        title: '温馨提示',
        content: '确认删除所有搜索记录？',
        success(res) {
          if (res.confirm) {
            that.history = []
          } else if (res.cancel) {
            console.log('用户点击取消')
          }
        }
      })
    }
  }
}
